.activity {
  -ms-flex-align: center;
      align-items: center;
  background: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0px auto;
  padding-bottom: 10px;
  position: -webkit-sticky;
