  font-weight: bold;
  height: 40px;
  margin-bottom: 6px;
  padding: 4px;
  text-align: center;
  width: 356px;
}

.activity-button {
  -ms-flex-align: center;
      align-items: center;
  border: 4px solid #58b0e0;
  color: #58b0e0;
  font-size: 16px;
  height: 40px;
  padding-top: 6px;
  width: 356px;
}

.activity-button:hover {
  background-color: #58e087;
  border: 4px solid #58e087;
  color: white;
}

@media (min-width: 900px) {
