    height: 60px;
    padding: 4px;
    width: 566px;
  }
  .activity-button {
    font-size: 26px;
    height: 60px;
    padding-top: 6px;
    width: 566px;
  }
}

@media (min-width: 1120px) {
  .activity input, .activity-sticky input {
    height: 60px;
    padding: 4px;
    width: 846px;
  }
  .activity-button {
    font-size: 36px;
    height: 60px;
    padding-top: 6px;
    width: 846px;
  }
}



