.calendar {
  background: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  position: -webkit-sticky;
  position: sticky;
  top: 94px;
  margin: 0px auto;
}

.daybox {
  -ms-flex-align: center;
      align-items: center;
  background-color: #93d6f9;
  border: 4px solid #58b0e0;
  color: #58b0e0;
  font-size: 16px;
  height: 50px;
  margin-bottom: 4px;
  margin-right: 1px;
  padding: 12px;
  text-align: center;
  width: 50px;
}

.last-daybox {
  margin-right: 0px;
}

.select-week {
  background-color: #93f9b7;
  border: 4px solid #58e087;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 1px auto;
  padding: 12px;
  text-align: center;
  width: 356px;
}

.weekdays {
  margin: 0px auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

@media (min-width: 900px) {
  .select-week {
    width: 566px;
  }
  .daybox {
    font-size: 30px;
    height: 80px;
    padding: 20px;
    width: 80px;
  }
  .calendar {
    top: 132px;
  }
}

@media (min-width: 1120px) {
  .select-week {
    width: 846px;
  }
  .daybox {
    font-size: 60px;
    height: 120px;
    padding: 28px;
    width: 120px;
  }
  .calendar {
    top: 136px;
  }
}



